import React, { useEffect } from 'react';

const sponsors = [
  {
    name: 'Sponsor One',
    img: 'https://via.placeholder.com/200x80?text=Sponsor+1',
    link: '#',
  },
  {
    name: 'Sponsor Two',
    img: 'https://via.placeholder.com/200x80?text=Sponsor+2',
    link: '#',
  },
  {
    name: 'Sponsor Three',
    img: 'https://via.placeholder.com/200x80?text=Sponsor+3',
    link: '#',
  },
];

export const CarouselSponsor = () => {
  useEffect(() => {
    if (typeof window !== 'undefined') {
      require('bootstrap/dist/js/bootstrap.bundle.min.js');
    }
  }, []);

  return (
    <div className="container my-4">
      <div id="sponsorCarousel" className="carousel slide" data-bs-ride="carousel">
        <div className="carousel-indicators">
          {sponsors.map((_, idx) => (
            <button
              key={idx}
              type="button"
              data-bs-target="#sponsorCarousel"
              data-bs-slide-to={idx}
              className={idx === 0 ? 'active' : ''}
              aria-current={idx === 0 ? 'true' : undefined}
              aria-label={`Slide ${idx + 1}`}
            />
          ))}
        </div>
        <div className="carousel-inner rounded shadow-sm bg-white">
          {sponsors.map((sponsor, idx) => (
            <div className={`carousel-item${idx === 0 ? ' active' : ''}`} key={sponsor.name}>
              <a href={sponsor.link} target="_blank" rel="noopener noreferrer" className="d-flex flex-column align-items-center justify-content-center py-4">
                <img src={sponsor.img} className="d-block mb-2" alt={sponsor.name} style={{ maxHeight: 80, maxWidth: 220, objectFit: 'contain' }} />
                <span className="fw-semibold text-dark" style={{ fontSize: '1.1rem' }}>{sponsor.name}</span>
              </a>
            </div>
          ))}
        </div>
        <button className="carousel-control-prev" type="button" data-bs-target="#sponsorCarousel" data-bs-slide="prev">
          <span className="carousel-control-prev-icon" aria-hidden="true"></span>
          <span className="visually-hidden">Previous</span>
        </button>
        <button className="carousel-control-next" type="button" data-bs-target="#sponsorCarousel" data-bs-slide="next">
          <span className="carousel-control-next-icon" aria-hidden="true"></span>
          <span className="visually-hidden">Next</span>
        </button>
      </div>
    </div>
  );
};

export default CarouselSponsor;
